import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { RoleMenuPermissionPostModel } from '../models/model';

@Injectable({
  providedIn: 'root'
})
export class RoleMenuPermissionService {
  apiUrl = `${environment.apiUrl}/api/RolePagePermission`;

  constructor(private http: HttpClient) { }

  get(roleId: string) {
    return this.http.get(`${this.apiUrl}?roleId=${roleId}`);
  }

  save(menus: RoleMenuPermissionPostModel) {
    return this.http.post(`${this.apiUrl}/list`, menus);
  }
}
