import { Component, OnInit } from '@angular/core';
import { TreeNode } from 'primeng/api';
import { forkJoin } from 'rxjs';
import { RoleMenuPermissionModel, RoleModel } from 'src/app/shared/models/model';
import { PageMasterService } from 'src/app/shared/services/page-master.service';
import { RoleMenuPermissionService } from 'src/app/shared/services/role-menu-permission.service';
import { RoleService } from 'src/app/shared/services/role.service';
import { ToastrService } from 'src/app/shared/services/toastr-message.service';
import { groupBy } from 'src/app/shared/utilities';

@Component({
  selector: 'app-menu-permission',
  templateUrl: './menu-permission.component.html'
})
export class MenuPermissionComponent implements OnInit {
  roles: RoleModel[] = [];
  menus: TreeNode[] = [];
  selectedMenus: TreeNode[] = [];
  roleMenuPermissions: RoleMenuPermissionModel = new RoleMenuPermissionModel();

  roleId!: string;
  display: boolean = false;

  constructor(private roleService: RoleService,
    private pageMasterService: PageMasterService,
    private roleMenuPermissionService: RoleMenuPermissionService,private toastrService: ToastrService) {
  }

  ngOnInit() {
    this.loadData();
  }

  mapMenusToTreeModel(menus: any[]) {
    const groupedByModule = groupBy(menus as any[], menu => menu.module);

    return groupedByModule.map(g => {
      const menuTreeMap = {
        key: g.groupBy,
        label: g.groupBy,
        icon: 'pi pi-fw pi-th-large',
        expanded: true,
        children: !!g?.data?.length ? g.data.map((s: any) => {
          return {
            key: s.pageName,
            label: s.pageName,
            data: s,
            icon: s.icon,
          };
        }) : []
      };
      return menuTreeMap;
    });
  }

  loadData() {
    forkJoin([this.roleService.getAll(), this.pageMasterService.get()]).subscribe(([roles, menus]) => {
      this.roles = roles as any[];
      this.menus = this.mapMenusToTreeModel(menus as any[]);

      if (this.roles.length) {
        this.roleId = this.roles[0].roleId;
        this.getRoleMenuPermissions();
      }
    });
  }

  getRoleMenuPermissions() {
    this.selectedMenus = [];
    this.roleMenuPermissions = new RoleMenuPermissionModel();

    this.roleMenuPermissionService.get(this.roleId).subscribe((response: any) => {

      if (!!response?.pageIds?.length) {
        this.roleMenuPermissions = response;

        const groupedByModuleTemp = this.mapMenusToTreeModel(response.pageIds as any[]);

        const dataMapping = response.pageIds.map((p: any) => {
          p['id'] = p.pageId;

          return {
            key: p.pageName,
            label: p.pageName,
            data: p,
            icon: p.icon,
            parent : groupedByModuleTemp.find(pt => pt.key === p.module)
          }
        });

        groupedByModuleTemp.forEach((sg: any) => {
          sg['parent'] = undefined;
          dataMapping.push(sg);
        });

        this.selectedMenus = dataMapping;
      }
    });
  }

  showDialog() {
    this.display = true;
  }

  onSubmit() {
    if (!this.roleId || !this.selectedMenus.length || (this.selectedMenus.length && this.selectedMenus.every(rm => !rm.parent))) {
      return;
    }

    const selectedMenus = this.selectedMenus.filter(rm => !!rm.parent).map(sm => {
      return { pageId: sm.data.id };
    });

    const data = {
      roleId: this.roleId,
      pageIds: selectedMenus
    }

    this.roleMenuPermissionService.save(data).subscribe((response: any) => {
      if (!!response.roleId) {
        this.roleMenuPermissions = response;
        this.toastrService.success('Role Menu Permission Updated Successfully.');
        this.display = false;
      }
    });
  }

  public close(): void {
    window.alert('Are u Sure Leave From');
    this.display = false;
  }

}

